import React, { useState, useRef, useEffect } from 'react';

const BudgetRangeSlider = ({ 
  minValue = 0, 
  maxValue = 1000000, 
  step = 10000,
  currentMin = 0,
  currentMax = 1000000,
  onRangeChange 
}) => {
  const [min, setMin] = useState(currentMin);
  const [max, setMax] = useState(currentMax);
  const [isDragging, setIsDragging] = useState(null);
  const sliderRef = useRef(null);
  const minHandleRef = useRef(null);
  const maxHandleRef = useRef(null);

  // Update internal state when props change
  useEffect(() => {
    setMin(currentMin);
    setMax(currentMax);
  }, [currentMin, currentMax]);

  const formatCurrency = (value) => {
    if (value === minValue) return 'No min';
    if (value === maxValue) return 'No max';
    return `$${(value / 1000).toFixed(0)}k`;
  };

  const getPercentage = (value) => {
    return ((value - minValue) / (maxValue - minValue)) * 100;
  };

  const getValueFromPosition = (clientX) => {
    if (!sliderRef.current) return minValue;
    
    const rect = sliderRef.current.getBoundingClientRect();
    const percentage = Math.max(0, Math.min(100, ((clientX - rect.left) / rect.width) * 100));
    const value = minValue + (percentage / 100) * (maxValue - minValue);
    
    // Round to nearest step
    return Math.round(value / step) * step;
  };

  const handleMouseDown = (e, type) => {
    e.preventDefault();
    setIsDragging(type);
    document.addEventListener('mousemove', handleMouseMove);
    document.addEventListener('mouseup', handleMouseUp);
  };

  // Removed handleTouchStart - now handled directly via DOM event listeners

  const handleMouseMove = (e) => {
    if (!isDragging) return;
    updateValue(e.clientX);
  };

  const handleTouchMove = (e) => {
    if (!isDragging) return;
    e.preventDefault();
    updateValue(e.touches[0].clientX);
  };

  const updateValue = (clientX) => {
    const newValue = getValueFromPosition(clientX);
    
    if (isDragging === 'min') {
      const newMin = Math.min(newValue, max - step);
      const finalMin = Math.max(minValue, newMin);
      setMin(finalMin);
      
      // Call callback during dragging for real-time updates
      if (onRangeChange) {
        onRangeChange({
          min: finalMin === minValue ? '' : finalMin,
          max: max === maxValue ? '' : max
        });
      }
    } else if (isDragging === 'max') {
      const newMax = Math.max(newValue, min + step);
      const finalMax = Math.min(maxValue, newMax);
      setMax(finalMax);
      
      // Call callback during dragging for real-time updates
      if (onRangeChange) {
        onRangeChange({
          min: min === minValue ? '' : min,
          max: finalMax === maxValue ? '' : finalMax
        });
      }
    }
  };

  const handleMouseUp = () => {
    setIsDragging(null);
    document.removeEventListener('mousemove', handleMouseMove);
    document.removeEventListener('mouseup', handleMouseUp);
    
    // Call the callback with final values
    if (onRangeChange) {
      onRangeChange({
        min: min === minValue ? '' : min,
        max: max === maxValue ? '' : max
      });
    }
  };

  const handleTouchEnd = () => {
    setIsDragging(null);
    document.removeEventListener('touchmove', handleTouchMove);
    document.removeEventListener('touchend', handleTouchEnd);
    document.removeEventListener('touchcancel', handleTouchEnd);
    
    // Call the callback with final values
    if (onRangeChange) {
      onRangeChange({
        min: min === minValue ? '' : min,
        max: max === maxValue ? '' : max
      });
    }
  };

  // Add touch event listeners directly to DOM elements to avoid passive event issues
  useEffect(() => {
    const minHandle = minHandleRef.current;
    const maxHandle = maxHandleRef.current;

    const handleMinTouchStart = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setIsDragging('min');
      document.addEventListener('touchmove', handleTouchMove, { passive: false });
      document.addEventListener('touchend', handleTouchEnd);
      document.addEventListener('touchcancel', handleTouchEnd);
    };

    const handleMaxTouchStart = (e) => {
      e.preventDefault();
      e.stopPropagation();
      setIsDragging('max');
      document.addEventListener('touchmove', handleTouchMove, { passive: false });
      document.addEventListener('touchend', handleTouchEnd);
      document.addEventListener('touchcancel', handleTouchEnd);
    };

    if (minHandle) {
      minHandle.addEventListener('touchstart', handleMinTouchStart, { passive: false });
    }

    if (maxHandle) {
      maxHandle.addEventListener('touchstart', handleMaxTouchStart, { passive: false });
    }

    return () => {
      if (minHandle) {
        minHandle.removeEventListener('touchstart', handleMinTouchStart);
      }
      if (maxHandle) {
        maxHandle.removeEventListener('touchstart', handleMaxTouchStart);
      }
    };
  }, []);

  const handleSliderClick = (e) => {
    if (isDragging) return;
    
    const newValue = getValueFromPosition(e.clientX);
    const minDistance = Math.abs(newValue - min);
    const maxDistance = Math.abs(newValue - max);
    
    if (minDistance < maxDistance) {
      const newMin = Math.min(newValue, max - step);
      setMin(Math.max(minValue, newMin));
    } else {
      const newMax = Math.max(newValue, min + step);
      setMax(Math.min(maxValue, newMax));
    }
    
    if (onRangeChange) {
      onRangeChange({
        min: min === minValue ? '' : min,
        max: max === maxValue ? '' : max
      });
    }
  };

  return (
    <div style={{ width: '100%', padding: '8px 0' }}>
      {/* Value display */}
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        marginBottom: '12px',
        fontSize: '12px',
        color: '#e2e8f0'
      }}>
        <span style={{ color: '#10b981', fontWeight: '600' }}>
          {formatCurrency(min)}
        </span>
        <span style={{ color: '#3b82f6', fontWeight: '600' }}>
          {formatCurrency(max)}
        </span>
      </div>

      {/* Slider container */}
      <div 
        ref={sliderRef}
        onClick={handleSliderClick}
        style={{
          position: 'relative',
          height: '6px',
          backgroundColor: '#475569',
          borderRadius: '3px',
          cursor: 'pointer',
          margin: '8px 0',
          touchAction: 'none',
          userSelect: 'none',
          WebkitUserSelect: 'none',
          MozUserSelect: 'none',
          msUserSelect: 'none'
        }}
      >
        {/* Active range */}
        <div
          style={{
            position: 'absolute',
            left: `${getPercentage(min)}%`,
            width: `${getPercentage(max) - getPercentage(min)}%`,
            height: '100%',
            backgroundColor: '#3b82f6',
            borderRadius: '3px',
            background: 'linear-gradient(90deg, #10b981 0%, #3b82f6 100%)'
          }}
        />

        {/* Min handle with larger touch area */}
        <div
          ref={minHandleRef}
          style={{
            position: 'absolute',
            left: `${getPercentage(min)}%`,
            top: '50%',
            transform: 'translate(-50%, -50%)',
            width: '44px',
            height: '44px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: isDragging === 'min' ? 20 : 10,
            touchAction: 'none',
            userSelect: 'none',
            WebkitUserSelect: 'none',
            MozUserSelect: 'none',
            msUserSelect: 'none'
          }}
          onMouseDown={(e) => handleMouseDown(e, 'min')}
        >
          <div
            style={{
              width: '24px',
              height: '24px',
              backgroundColor: '#10b981',
              border: '3px solid #ffffff',
              borderRadius: '50%',
              cursor: 'grab',
              boxShadow: '0 2px 6px rgba(16, 185, 129, 0.3)',
              transition: isDragging === 'min' ? 'none' : 'all 0.1s ease',
              pointerEvents: 'none'
            }}
          />
        </div>

        {/* Max handle with larger touch area */}
        <div
          ref={maxHandleRef}
          style={{
            position: 'absolute',
            left: `${getPercentage(max)}%`,
            top: '50%',
            transform: 'translate(-50%, -50%)',
            width: '44px',
            height: '44px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: isDragging === 'max' ? 20 : 10,
            touchAction: 'none',
            userSelect: 'none',
            WebkitUserSelect: 'none',
            MozUserSelect: 'none',
            msUserSelect: 'none'
          }}
          onMouseDown={(e) => handleMouseDown(e, 'max')}
        >
          <div
            style={{
              width: '24px',
              height: '24px',
              backgroundColor: '#3b82f6',
              border: '3px solid #ffffff',
              borderRadius: '50%',
              cursor: 'grab',
              boxShadow: '0 2px 6px rgba(59, 130, 246, 0.3)',
              transition: isDragging === 'max' ? 'none' : 'all 0.1s ease',
              pointerEvents: 'none'
            }}
          />
        </div>
      </div>

      {/* Range labels */}
      <div style={{ 
        display: 'flex', 
        justifyContent: 'space-between', 
        marginTop: '8px',
        fontSize: '10px',
        color: '#94a3b8'
      }}>
        <span>$0</span>
        <span>$1M+</span>
      </div>
    </div>
  );
};

export default BudgetRangeSlider;
